const initialState = {
    allCountry:[],
    setCountry:[],
};
//"react-router-dom": "^5.3.3",

function reducer (state=initialState, action){
    switch(action.type){
        case 'GET_COUNTRIES':
            return {
                ...state,
                allCountry: action.payload,
                setCountry:action.payload
            }
            default:
                return{
                    ...state
            }
    }
}

export default reducer;